.journey {
    width: 100%;
    min-height: 380px;
    margin: 70px 0 0 0;
}


.journey .nature_block .gle {
    position: relative;
}

.journey .nature_img img {
    cursor: pointer;
    width: 100%;
    height: 150px;
    object-fit: cover;
    object-position: center;
}

.journey .nature_img .name {
    position: absolute;
    z-index: 9999;
    font-size: 22px;
    color: #eaf5ef;
    padding: 33px 20px;
}

.journey .nature_img .name {
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    background: rgba(0, 0, 0, 0.3);
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.journey .nature_block {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(410px, 1fr));
    gap: 20px;
    grid-auto-flow: dense;
    margin-top: 20px;
}

.journey .relative {
    position: relative;
}

.journey .cultural {
    width: 100%;
    min-height: 244px;
    background: #f6f3f3;
    padding: 20px 20px 20px 20px;
    position: relative;
    z-index: 9999;
    margin: -4px 0 0 0;
}


.journey .cultural ul li h4{
    margin-left: 35px;
}
.journey .cultural ul li h4:hover{
    text-decoration: underline;
    color: #14ae5c;
}
.journey .cultural ul li h4::before {
    position: absolute;
    content: url("../../img/arrow.svg");
    margin-left: -30px;
    margin-top: 2px;
    width: 50px;
}

.journey .cultural ul li a {
    color: #000;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 110%;
}
.journey .category_one{
    border: 1px solid #ddd;
    padding: 10px 15px;
    width: 100%;
}

.journey  .a_teg{
    color: #0091cc;
    font-size: 16px;
    font-weight: 400;
}
.journey  .a_teg:hover{
    text-decoration: underline;
}
.journey .journey_none{
    display: none;
}
@media (max-width: 550px) {
    .journey .journey_block{
        display: none;
    }
    .journey .nature_img img {
        cursor: pointer;
        width: 95%;
        height: 90px;
        object-fit: cover;
        object-position: center;
    }
    .journey .nature_img .name {
        position: absolute;
        top: 0;
        left: 0;
        content: "";
        background: rgba(0, 0, 0, 0.3);
        width: 95%;
        height: 100%;
        cursor: pointer;
    }
    .journey .cultural {
        width: 95%;
        min-height: 244px;
        background: #f6f3f3;
        padding: 20px 20px 20px 20px;
        position: relative;
        z-index: 9999;
        margin: -4px 0 0 0;
    }
    .journey .journey_none{
        display: block;
        margin: 10px 0 0 0;
    }
    .journey .nature_block {
        margin-left: 8px;
    }
}
