.tour_rewies {
    margin: 20px 0 0 0;
}

.tour_rewies .icon {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}

.tour_rewies .input_box {
    position: relative;
}

.tour_rewies .tour_li {
    width: 100%;
    min-height: 200px;
    padding: 25px;
    background: #fff;
    border: 1px solid #dddddd;
}

.tour_rewies .category_box_two {
    margin-bottom: 20px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 14px;
    gap: 8px;
    border: 1px solid #14ae5c;
    background: #fff;
    box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
    color: #0f0f0f;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    line-height: 1;
    outline: none;
}

.tour_rewies .category_box_one {
    margin-bottom: 20px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 14px;
    gap: 8px;
    border: 1px solid #14ae5c;
    background: #fff;
    box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
    color: #0f0f0f;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    line-height: 1;
    outline: none;
}

.tour_rewies .category_box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 14px;
    gap: 8px;
    border: 1px solid #14ae5c;
    background: #fff;
    box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
    color: #0f0f0f;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    line-height: 1;
    outline: none;
}

.star_block {
    display: flex;
}

.tour_rewies .text_ares {
    width: 100% !important;
    margin-top: 25px;
    min-height: 100px;
    padding: 10px 14px;
    border: 1px solid #14ae5c;
    font-size: 14px;
    outline: none;
    background: #fff;
    box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
    transition: all 0.3s linear;
}

.tour_rewies .btn {
    margin-top: 15px;
    margin-right: 15px;
    padding: 10px 70px;
    outline: none;
    border: none;
    cursor: pointer;
    color: #fcfcfc;
    font-size: 16px;
    font-weight: 400;
    background: var(--green);
}

.tour_rewies .select_input {
    top: 0;
    position: absolute;
    background: #00000000;
    width: 100%;
    height: 42px;
    cursor: pointer;
}

.tour_rewies .fill {
    font-size: 25px;
    margin-left: 7px;
    margin-top: 23px;
}

.tour_rewies .rewie {
    margin-top: 30px;
}

.tour_rewies .date {
    margin-bottom: 10px;
}

.tour_rewies .date h2 {
    color: #000;
    font-size: 18px;
    font-weight: 600;
}

.tour_rewies .created {
    display: block;
    border-top: 1px solid #ddd;
    padding: 12px 15px;
    color: #666;
    font-size: 12px;
    font-weight: 500;
}

.tour_rewies .panel {
    margin-bottom: 20px;
    background-color: #fff;
    border: 1px solid transparent;
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
    box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
}

.tour_rewies .panel-default {
    border-color: #ddd;
}

.tour_rewies .panel-heading {
    padding: 10px 15px;
    border-bottom: 1px solid transparent;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.tour_rewies .panel-default > .panel-heading {
    color: #333;
    font-size: 14px;
    background-color: #f5f5f5;
    border-color: #ddd;
}

.tour_rewies .panel-body p {
    padding: 15px;
    color: #111;
    font-size: 14px;
    font-weight: 400;
    line-height: 1;
}

@media (max-width: 500px) {
    .tour_rewies .fill {
        font-size: 20px;
    }
    .tour_rewies .btn {
        padding: 10px 50px;
    }
}

@media (max-width: 370px) {
    .tour_rewies .fill {
        font-size: 18px;
    }
    .tour_rewies .btn {
        padding: 8px 45px;
    }
}

@media (max-width: 350px) {
    .tour_rewies .fill {
        font-size: 18px;
    }
    .tour_rewies .btn {
        padding: 8px 40px;
    }
}
@media (max-width: 339px) {
    .tour_rewies .fill {
        font-size: 18px;
    }
    .tour_rewies .btn {
        padding: 8px 30px;
    }
}