.section {
    margin: 60px auto;
}

.main-tour-list {
    margin-bottom: 10px;
}

.section .border1ccc {
    border: 1px solid #ccc;
    width: 100%;
}

.section .date_m {
    display: flex;
    flex-direction: column;
    background-color: #14ae5c;
    color: white;
    padding: 8px 0;
    text-align: center;
    vertical-align: middle !important;
    margin: 5px 0;
    width: 55px;
}

.section .date_m span {
    font-weight: 300;
}

.section .date_flex {
    background-color: #fff;
    border: 1px solid #ddd;
    padding: 8px 15px 8px 6px;
    width: 60%;
    display: grid;
    justify-content: space-between;
    grid-template-columns: 0.6fr 2.5fr 1fr 0.5fr 0.5fr;
    align-items: center;
    font-size: 14px;
}

.tour_duration {
    display: flex;
    flex-direction: column;
}

.tour_duration b {
    font-size: 16px;
    line-height: 1;
}

.tour_duration small {
    font-size: 12px;
    color: #444;
    line-height: 1;
}

.tour_name {
    display: block;
    color: #111;
    text-decoration: underline;
    padding: 6px 0;
}

.section .dates {
    display: flex;
    width: 60%;
    align-items: center;
}

.section .dates h2 {
    color: #14ae5c;
    font-weight: 500;
    font-size: 22px;
    margin-bottom: 10px;
}

.section .dates h3 {
    font-weight: 500;
    font-size: 15px;
    margin-bottom: 10px;
}

.section .twoo {
    display: none;
}

@media (max-width: 820px) {
    .section .date_flex {
        width: 100%;
    }
}

@media (max-width: 508px) {
    .tour_duration small {
        font-size: 15px;
        font-weight: 500;
        color: #444;
        line-height: 1;
    }

    .section .one {
        display: none;
    }

    .section .twoo {
        display: block;
    }

    .section .date {
        background-color: #fff;
        border: 1px solid #ddd;
        padding: 10px;
        width: 100%;
        font-size: 14px;
    }

    .section .all_flex {
        display: flex;
        align-items: center;
        border-bottom: 1px solid #f3f3f3;

    }

    .section .date_m {
        display: block;
        background-color: #14ae5c;
        color: white;
        padding: 5px 0;
        vertical-align: middle !important;
        margin: 5px 0;
    }

    .section .date_m span {
        margin: 0 0 0 5px;
    }

    .tour_name {
        margin: 0 0 0 20px;
    }

    .flex__all {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 5px 0 0 0;
    }
}

@media (max-width: 390px) {
    .section .date_flex {
        display: grid;
        justify-content: space-between;
        grid-template-columns: 0.7fr 2.5fr 1fr;
        padding: 15px 15px 15px 15px;
    }
    .tour_duration small {
        font-size: 15px;
        font-weight: 500;
        color: #444;
        line-height: 1;
    }
    .tour_name {
        margin: 0 25px;
    }
}
@media (max-width: 380px) {
    .section .date_flex {
        display: grid;
        justify-content: space-between;
        grid-template-columns: 0.7fr 2.5fr 1fr;
        padding: 15px 15px 15px 15px;
    }
    .tour_duration small {
        font-size: 15px;
        font-weight: 500;
        color: #444;
        line-height: 1;
    }
    .tour_name {
        margin: 0 18px;
    }
}