.about_the_company {
  margin-top: 60px;
  width: 100%;
}

.about_the_company .container {
  width: 100%;
}

.about_the_company .wrapper {
  width: 100%;
}

.about_the_company .wrapper p {
  margin-top: 10px;
  color: #000;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.3;
}

.about_the_company .best_tour {
  margin-top: 10px;
  width: 100%;
}

.about_the_company .option_block {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 70px;
  margin-top: 40px;
}

.about_the_company .experience_box {
  width: 100%;
}

.about_the_company .experience_box h3 {
  color: #0f0f0f;
  font-size: 20px;
  font-weight: 500;
}

.about_the_company .experience_box p {
  margin-top: 10px;
  color: #767681;
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: 20px;
}

.about_the_company .wrapper p span {
  font-size: 14px;
  font-weight: 600;
  color: #000;
}

@media (max-width: 500px) {
  .about_the_company .best_tour h1,
  .about_the_company .wrapper h1 {
    font-size: 20px;
    line-height: 20px;
    text-align: start;
  }

  .about_the_company .wrapper p {
    font-size: 14px;
  }

  .about_the_company .best_tour {
    margin-top: 60px;
  }
}

@media (max-width: 400px) {
  .about_the_company .option_block {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}
