.nav-block {
  background: linear-gradient(90deg, #00970f, #04c700);
  display: flex;
  align-items: center;
  pointer-events: none;
}

.nav-block-item {
  color: #fff;
  padding: 15px 20px;
  cursor: pointer;
  pointer-events: all;
}

.border {
  height: 25px;
  border-right: 1px solid #fff;
}

.nav-block-item a {
  color: #fff;
}

.choose .choose-title {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.select_header {
  position: absolute;
  top: 75%;
  background: #fff;
  color: #000;
  margin-left: -18px;
  margin-top: 14px;
  border: 1px solid #bdbdbd;
  min-width: 160px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
  width: 200px;
  min-height: 45px;
  transition: opacity 0.3s ease;
  z-index: 9999 !important;
  padding: 0 20px 0 20px;
  display: none;
}

.choose {
  position: relative;
}

.choose:hover .select_header,
.select_header:hover {
  display: block;
}

.select-list {
  list-style-type: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0;
  z-index: 9999;
  margin-left: -12px;
}

.select-list li {
  position: relative;
  color: #000;
  padding: 8px;
}

.select-list li:hover {
  display: block;
  text-decoration: underline;
  color: #04c700;
  cursor: pointer;
}

@media (max-width: 800px) {
  .navlinkbar {
    display: none;
  }
}
