.our_team {
  padding-top: 60px;
  width: 100%;
  min-height: 450px;
}
.our_team .team_block {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 10px;
  display: flex;
  align-items: center;
}
.our_team .team_box1 {
  width: 50%;
  min-width: 0;
  overflow: hidden;
}
.our_team .team_box1 img{
  width: 80%
}
.our_team .team_box2 {
  width: 100%;
}

.our_team .team_box2 p {
  color: #0f0f0f;
  font-size: 14px;
  font-weight: 300;
  line-height: 19px;
  margin-top: 20px;
}
.our_team .team_box1 h2 {
  color: #363636;
  text-align: center;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  line-height: 32px;
  margin-top: 20px;
}
.our_team .team_box1 p {
  color: #767681;
  text-align: center;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
}
.our_team .collaborator {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.our_team .collaborator img {
  width: 100%;
  height: 315px;
  border-radius: 20px;
  box-shadow: -5px 60px 100px -39px rgba(0, 0, 0, 0.15);
}

@media (max-width: 500px) {
  .our_team {
    padding-top: 60px;
  }
  .our_team .team_box1 h2 {
    font-size: 20px;
  }
}
@media (max-width: 400px) {
  .our_team .team_block {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
  .our_team .team_box2 h2 {
    font-size: 25px;
    line-height: 15px;
  }
  .our_team .team_box2 p {
    font-size: 13px;
  }
}
