.customer_rewievs {
  width: 100%;
  min-height: 1000px;
}
.customer_rewievs .our_clients {
  margin-top: 30px;
  color: #000;
  font-size: 30px;
  font-weight: 500;
  line-height: normal;
}
.customer_rewievs .star_block {
  display: flex;
  align-items: center;
}
.customer_rewievs .star {
  font-size: 25px;
  color: #ddd;
  cursor: pointer;
  transition: color 0.3s;
}
.customer_rewievs .star.active,
.star:hover {
  color: #ffce00;
}
.customer_rewievs .our_clients_read {
  color: #000;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  width: 60%;
  margin-top: 6px;
}
.customer_rewievs .rewievs_block {
  margin-top: 30px;
  width: 100%;
  min-height: 245px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.customer_rewievs .box_customer {
  display: grid;
  grid-template-columns: 1fr 2.5fr;
  gap: 15px;
  padding: 20px;
  border: 1px solid #14ae5c;
  background: #eaf5ef;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.05);
}
.customer_rewievs .customer_photo_block,
.customer_rewievs .customer_text_block {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.customer_rewievs .customer_text_block p {
  color: #5b5959;
  font-size: 14px;
  font-weight: 300;
  line-height: normal;
}
.customer_rewievs .customer_photo_block h3 {
  display: flex;
  color: #0f0f0f;
  font-size: 16px;
  font-weight: 500;
  line-height: normal;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.customer_rewievs .customer_photo_block h3 > p {
  margin-left: 5px;
  text-align: center;
}
.customer_rewievs .customer_photo_block img {
  width: 136px;
  height: 136px;
  border-radius: 50%;
}
.customer_rewievs .impressions_block {
  margin-top: 40px;
  width: 100%;
  min-height: 350px;
  padding: 25px;
  background: #fff;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
}
.customer_rewievs .impressions_block h3 {
  color: #14ae5c;
  font-size: 22px;
  font-weight: 500;
  line-height: 39px;
}
.customer_rewievs .customer_registr_block {
  margin-top: 20px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.customer_rewievs .customer_registr_block input,
.customer_rewievs .customer_option_tour {
  padding: 10px 14px;
  border: 1px solid #14ae5c;
  background: #fff;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
  color: #0f0f0f;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  outline: none;
}
.customer_rewievs .customer_registr_block input::placeholder {
  color: #0f0f0f;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
}
.customer_rewievs .textarea {
  max-width: 20%;
  min-width: 100%;
  margin-top: 25px;
  min-height: 100px;
  padding: 10px 14px;
  border: 1px solid #14ae5c;
  font-size: 14px;
  font-weight: 300;
  outline: none;
  background: #fff;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
  -webkit-transition: all 0.3s linear;
  -moz-transition: all 0.3s linear;
  -o-transition: all 0.3s linear;
  transition: all 0.3s linear;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
.customer_rewievs .btn_mark_block {
  display: flex;
  align-items: center;
  margin-top: 20px;
  flex-wrap: wrap;
}
.customer_rewievs .btn_mark_block button {
  width: 249px;
  padding: 10px;
  background: #14ae5c;
  color: #fcfcfc;
  font-size: 16px;
  font-weight: 500;
  line-height: normal;
  border: none;
  outline: none;
  cursor: pointer;
}
.customer_rewievs .btn_mark_block h4 {
  margin-left: 20px;
  color: #414141;
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
}
.customer_rewievs .star {
  margin-left: 8px;
  cursor: pointer;
}
.customer_rewievs .star_two {
  margin-left: 8px;
  cursor: pointer;
}
.customer_rewievs .star_two.filled {
  color: gold;
}
.customer_rewievs .input_file {
  margin-top: 20px;
}
.customer_rewievs .fistart {
  color: #ffce00;
}
.customer_rewievs .pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}
.customer_rewievs .pagination button {
  margin: 0 0;
  padding: 5px 5px;
  cursor: pointer;
}
.customer_rewievs .pagination h5 {
  color: #121;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.customer_rewievs .pagination button {
  border: none;
  background: transparent;
}
.customer_rewievs .pagination .arrow_left {
  font-size: 25px;
}
.customer_rewievs .pagination .arrow_right {
  font-size: 25px;
  margin: 3px 0 0 0;
}

@media (max-width: 900px) {
  .customer_rewievs .rewievs_block {
    display: grid;
    grid-template-columns: 1fr;
  }
}
@media (max-width: 600px) {
  .customer_rewievs .box_customer {
    display: grid;
    grid-template-columns: 1fr;
  }
  .customer_rewievs .impressions_block h3 {
    color: #14ae5c;
    font-size: 20px;
  }
  .customer_rewievs .customer_registr_block {
    display: grid;
    grid-template-columns: 1fr;
  }
  .customer_rewievs .our_clients {
    color: #000;
    font-size: 20px;
  }
  .customer_rewievs .our_clients_read {
    font-size: 14px;
  }
  .customer_rewievs .btn_mark_block h4 {
    margin-left: 6px;
    margin-top: 15px;
  }
  .customer_rewievs .star_block {
    display: flex;
    align-items: center;
    margin: 17px 0 0 0;
  }
}
