#article_detail .article_detail {
  display: flex;
  justify-content: center;
}

#article_detail .article_detail .article_all p {
  font-weight: 300;
  font-size: 14px;
  color: #000;
}

#article_detail .article_detail .article_all p h2 {
  font-weight: 300;
  font-size: 14px;
  color: black;
}

#article_detail .article_detail .article_all h3 {
  font-size: 18px;
  font-weight: 500;
  color: black;
  margin: 10px 0 10px 0;
}

.article_all a {
  display: table;
  padding: 12px 25px;
  margin: 40px auto 0 auto;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  background-color: #14ae5c;
}

#article_detail .category h3 {
  color: #333;
  background-color: #f5f5f5;
  border-color: #ddd;
  font-size: 18px;
  font-weight: 500;
  padding: 5px 15px;
  border-bottom: 1px solid #ddd;
}

#article_detail .article_detail_one {
  display: grid;
  grid-template-columns: 2.5fr 1fr;
  gap: 30px;
  margin: 25px 0 0 0;
}

#article_detail .category .category_one {
  border: 1px solid #ddd;
  padding: 10px 15px;
}

#article_detail .a_teg {
  color: #0091cc;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
}

#article_detail .a_teg:hover {
  text-decoration: underline;
}

.article-img {
  width: 100%;
}

@media (max-width: 700px) {
  #article_detail .article_detail_one {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
    margin: 25px 0 0 0;
  }
}
