.home {
  margin: 30px auto;
  position: relative;
}

.home::before {
  content: '';
  background-size: 1000px;
  background-position: right bottom;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: -230px;
  z-index: -1;
}

.home .tour_all {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(310px, 1fr));
  grid-gap: 32px;
  grid-auto-flow: dense;
}

.home .tour_all .tour_box {
  cursor: pointer;
  width: 100%;
  height: 240px;
  position: relative;
  overflow: hidden;
}

.home .tour_all .tour_image img {
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  object-position: center;
}

.home .tour_all .tour_image,
.home .tour_all .content {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}

.home .tour_all .tour_image {
  z-index: 1;
}

.home .tour_all .content {
  bottom: 0;
  padding: 13px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  z-index: 10;
}

.home .tour_all .content_block {
  width: 100%;
  display: flex;
  background-color: rgba(0, 0, 0, 0.3);
  align-items: center;
  position: absolute;
  bottom: 0;
  padding: 10px;
  z-index: 15;
}

.content_block .content_box {
  width: 100%;
}

.context_box-item {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tour-title,
.tour-price {
  color: #fff;
  font-size: 18px;
  font-weight: 500;
}

/* .context_box-item p:last-child {
  font-size: 24px !important;
  font-weight: 600 !important;
  margin-left: 15px !important;
} */

.tour-date {
  margin-top: 5px;
  color: #fff;
  font-size: 14px;
  font-weight: 300;
  display: flex;
  align-items: center;
}

.home .content .box_h1 {
  color: #fff;
  font-size: 24px !important;
  font-weight: 600 !important;
  margin-left: 15px !important;
}

.home button {
  padding: 10px 30px;
  color: #ffffff;
  font-size: 16px;
  font-weight: 400;
  border: none;
  background-color: #14ae5c;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 32px 0 0 0;
}

.home .home_none {
  display: none;
}

.home-title {
  text-align: center;
  margin-bottom: 20px;
}

.description {
  font-size: 18px;
  margin-bottom: 20px;
}

.kg-desc {
  margin-top: 35px;
  font-size: 18px;
  line-height: 24px;
}

@media (max-width: 550px) {
  .home .nome_block {
    display: none;
  }
  .home .home_none {
    display: block;
  }
  .home .div {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }

  .description {
    font-size: 16px;
  }

  .kg-desc {
    font-size: 16px;
    line-height: 20px;
  }
}

@media (max-width: 428px) {
  .home .tour_all .tour_box {
    width: 100%;
    height: 140px;
    margin: 0 auto;
  }

  .home button {
    margin: 20px 0 0 0;
  }
}
