#article {
  margin-top: 50px;
  width: 100%;
}

.article-title {
  text-align: center;
  margin-bottom: 20px;
}

.article-desc {
  font-size: 18px;
  margin-bottom: 40px;
}

#article .article_grit {
  display: grid;
  grid-template-columns: 2.5fr 1fr;
  gap: 30px;
}

#article .tour_box {
  display: flex;
  margin-bottom: 20px;
  border: 1px solid #ddd;
}

#article .tour_box img {
  width: 34%;
}

#article .tour_box .content_box h3 {
  font-weight: 500;
  color: #000;
  font-size: 20px;
  line-height: 18px;
  margin: 20px 20px 0 20px;
  height: 34px;
  overflow: hidden;
}

#article .tour_box .content_box h2 {
  font-weight: 300;
  color: #000;
  font-size: 14px;
  line-height: 16px;
  padding: 15px 20px 0 20px;
  height: 96px;
  overflow: hidden;
}

#article .tour_box .content_box p {
  overflow: hidden;
  height: 100px;
  margin-bottom: 10px;
  margin-left: 10px;
  color: #4f5d6e;
  font-size: 16px;
}

#article .tour_box .content_box .btn {
  overflow: hidden;
  padding: 12px 25px;
  margin: 20px;
  font-size: 14px;
  background: #14ae5c;
  color: #fff;
  border: none;
  cursor: pointer;
}

#article .category p {
  color: #333;
  background-color: #f5f5f5;
  border-color: #ddd;
  font-size: 18px;
  font-weight: 500;
  padding: 5px 15px;
  border-bottom: 1px solid #ddd;
  cursor: pointer;
}

#article .category .category_one {
  border: 1px solid #ddd;
  padding: 10px 15px;
}

#article .a_teg {
  color: #0091cc;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
}
#article .a_teg:hover {
  color: #0091cc;
}
#article .a_teg:hover {
  text-decoration: underline;
}

@media (max-width: 700px) {
  #article .article_grit {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

@media (max-width: 570px) {
  .article-desc {
    font-size: 16px;
  }
}

@media (max-width: 500px) {
  #article .tour_box {
    display: block;
    margin-bottom: 20px;
    border: 1px solid #ddd;
  }
  #article .tour_box img {
    width: 100%;
  }
}
