.lang-btn {
  color: #fff;
  font-size: 14px;
  background-color: #14ae5c;
  border: none;
  padding: 5px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 570px) {
  .lang-btn {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
}
