.burger {
  display: none;
  position: relative;
  z-index: 9999;
}

.burger-form {
  display: none;
}

#toggle,
.hamburger {
  display: none;
}

.hamburger {
  width: 3rem;
  height: 45px;
  z-index: 5;
  cursor: pointer;
}

.hamburger div {
  position: relative;
  width: 2.8em;
  height: 5px;
  border-radius: 3px;
  margin-top: 5px;
  background: #14ae5c;
  transition: all 0.3s ease-in-out;
}

#toggle:checked + .hamburger .top-bun {
  transform: rotate(-45deg);
  margin-top: 13px;
}

#toggle:checked + .hamburger .bottom-bun {
  opacity: 0;
  transform: rotate(45deg);
}

#toggle:checked + .hamburger .meat {
  transform: rotate(45deg);
  margin-top: -5px;
}

@media (max-width: 800px) {
  .burger-form {
    display: block;
    cursor: pointer;
    position: relative;
    z-index: 9999;
  }

  .hamburger {
    display: block;
    padding: 1px 0 0 0;
    margin-left: auto;
    margin-top: 6px;
  }

  .burger {
    display: block;
    position: relative;
    z-index: 9999;
  }
}
