.create_tours {
  width: 100%;
  min-height: 500px;
}
.create_tours .block_constructor {
  width: 100%;
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(310px, 1fr));
  gap: 40px;
}
.create_tours .box {
  width: 100%;
}

.create_tours .box p {
  margin-top: 20px;
  color: #000;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.1;
}
.create_tours button {
  cursor: pointer;
}
.create_tours .create_button1,
.create_tours .create_button2 {
  width: 250px;
  height: 45px;
  margin: 15px 20px 0 0;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #14ae5c;
  background: #14ae5c;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  border-radius: 10px;
}
.create_tours .block_img_tour {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  grid-template-columns: 1fr 1fr;
  margin-left: auto;
}
.create_tours .block_img_tour img {
  width: 90%;
  height: 90%;
  object-fit: cover;
}
.create_tours .slider_blocks {
  width: 100%;
  display: none;
}
@media (max-width: 900px) {
  .create_tours .create_button1,
  .create_tours .create_button2 {
    width: 100%;
  }
}
@media (max-width: 749px) {
  .create_tours .create_button1,
  .create_tours .create_button2 {
    width: 400px;
  }
}
@media (max-width: 700px) {
  .create_tours .block_img_tour {
    display: none;
  }
  .create_tours .slider_blocks {
    width: 100%;
    display: block;
  }
}
@media (max-width: 550px) {
  .create_tours .box h2 {
    font-size: 22px;
  }
  .create_tours .box p {
    font-size: 16px;
  }
}
@media (max-width: 500px) {
  .create_tours .create_button1,
  .create_tours .create_button2 {
    width: 100%;
    height: 40px;
    font-size: 14px;
  }
}
@media (max-width: 400px) {
  .create_tours .block_constructor {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}
