#prise {
    display: flex;
    flex-direction: column;
    margin: 15px 0 0 0;
}

#prise .prise_one {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #fff;
    box-shadow: 0 2px 6px rgba(0, 0, 0, .2);
    border-radius: 5px;
    margin-bottom: 25px;
    font-size: 14px;
    padding: 10px;
}

#prise .prise_one h2 {
    color: #333;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
}

#prise .prise_one p {
    color: #333;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.25;
}

#prise .prise_one h2 span {
    color: #333;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.25;
    letter-spacing: -.2px;
}

#prise .prise_one h6 {
    color: #000;
    font-size: 14px;
    font-weight: 300;
}

#prise .prise_btn {
    width: 100px;
    height: 26px;
    background: green;
    border: none;
    color: #fff;
    font-size: 15px;
}

#modal_prise_one {
    position: fixed;
    top: auto;
    left: 0;
    width: 100%;
    height: 100%;
    bottom: 0;
    background: rgba(79, 78, 78, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    overflow-y: scroll;
}


#modal_prise_one .prise_modal {
    width: 90%;
    border: 1px solid #ddd;
    -webkit-backdrop-filter: blur(50px);
    background-color: #ffffff;
    padding: 30px;
    height: 500px;
}

#modal_prise_one .prise_modal .scrollable-content {
    max-height: 100%;
    overflow-y: auto;
}

#prise .next {
    transform: rotate(90deg);
    font-size: 20px;
}

#prise .prise_one_twoo {
    background: #fff;
    box-shadow: 0 2px 6px rgba(0, 0, 0, .2);
    border-radius: 5px;
    margin-bottom: 25px;
    font-size: 14px;
    padding: 10px;
    display: none;
    cursor: pointer;
}

#prise .prise_one_twoo .tue {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 40px;
}
#prise .prise_one_twoo .tue p {
    font-size: 18px;
    font-weight: 400;
}

#prise .sold {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    margin: 20px 0 0 0;
}
#prise .sold h2 {
    font-size: 16px;
    font-weight: 400;
}

@media (max-width: 428px) {
    #prise .prise_one_twoo {
        display: block;
    }
    #prise .prise_one {
       display: none;
    }
}