.cuaranteed_tours {
  width: 100%;
}

.cuaranteed_tours .wrapper {
  width: 100%;
  display: grid;
}

.cuaranteed_tours .tours_all {
  margin-top: 30px;
  width: 100%;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 30px;
}

.cuaranteed_tours .slide .slide_block_two img {
  width: 100%;
  height: 350px;
  object-fit: cover;
  object-position: center;
}

.images_img {
  margin: -101px 0 0 0;
  position: absolute;
}

.cuaranteed_tours .slide .slide_block {
  height: 300px;
  padding: 10px;
  border: none;
}

.cuaranteed_tours .slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 246px 0 -152px;
  padding: 0;
  cursor: pointer;
}

.cuaranteed_tours .slide .slide_block_two {
  height: 460px;
}

.cuaranteed_tours .wrapper .wrapper_one {
  width: 100%;
  min-width: 0;
  overflow: hidden;
}

.cuaranteed_tours .wrapper .wrapper_two {
  width: 100%;
}

.cuaranteed_tours .wrapper .wrapper_one p {
  font-size: 14px !important;
  font-weight: 300 !important;
}

.cuaranteed_tours .wrapper .wrapper_two h1 {
  color: #1a1a1a;
  font-size: 32px;
  font-weight: 700;
  margin-top: -10px;
}

.cuaranteed_tours .wrapper .wrapper_two h2 {
  color: #000;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
}

.cuaranteed_tours .wrapper .colored-star {
  color: yellow;
}

.cuaranteed_tours .text {
  margin: 15px 0;
}

.cuaranteed_tours .price {
  display: grid;
  grid-template-columns: 120px 1fr;
  gap: 10px;
  margin-bottom: 25px;
}

.price_div {
  outline: none;
  border: none;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.32px;
  background: green;
}

.cuaranteed_tours .price p {
  color: #000;
  font-size: 12px;
  font-weight: 400;
}

.cuaranteed_tours .route {
  margin: 20px 0 0 0;
  color: #1a1a1a;
  font-size: 32px;
  font-weight: 700;
}

.cuaranteed_tours .cate {
  width: 100%;
  min-height: 50px;
  padding: 20px;
  margin-bottom: 15px;
  cursor: pointer;
  border: 1.5px solid #14ae5c;
  transition: background-color 0.3s;
}

.cuaranteed_tours .cate.active {
  background: #fcfcfc;
  color: #000;
}

.cuaranteed_tours .cate .finish {
  margin: 10px 0;
}

.cuaranteed_tours .cate .finish p {
  color: green;
  font-size: 14px;
  font-weight: 300;
}

.cuaranteed_tours .cate .manu_cate p {
  color: #000;
  font-size: 14px;
  font-weight: 300;
}

.cuaranteed_tours .cate .top {
  margin-top: 5px;
}

.cuaranteed_tours .cate h4 {
  font-size: 18px;
  font-weight: 600;
}

.cuaranteed_tours .sit_amet {
  display: flex;
  align-items: center;
}

.cuaranteed_tours .magic {
  margin-right: 10px;
}

.cuaranteed_tours .icon_cate {
  font-weight: 600;
}

.cuaranteed_tours .request {
  width: 100%;
  min-height: 0;
  padding-bottom: 10px;
  background: #fff;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.09);
}

.cuaranteed_tours .request p {
  color: #000;
  font-size: 13px;
  font-weight: 400;
}

.cuaranteed_tours .request .boxx {
  width: 100%;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 14px;
  background: green;
}

.cuaranteed_tours .request .boxx p {
  color: #fff;
  font-size: 14px;
  font-weight: 500;
}

.cuaranteed_tours .request .box {
  width: 100%;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 14px;
}

.cuaranteed_tours .hr {
  margin: 15px 0;
  width: 100%;
  border: 1px solid green;
}

.cuaranteed_tours .send_text {
  padding: 0 14px;
  text-align: center;
}

.cuaranteed_tours .request .send {
  display: flex;
  margin: 0 auto;
  margin-top: 20px;
  padding: 10px 30px;
  outline: none;
  border: none;
  cursor: pointer;
  background: green;
  color: #fcfcfc;
  font-size: 18px;
  font-weight: 500;
}

.cuaranteed_tours .loading_div {
  width: 100%;
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cuaranteed_tours .buttons_category {
  width: 100%;
  margin: 60px 0 30px 0;
  overflow-x: auto;
  white-space: nowrap;
}

.cuaranteed_tours .buttons_category .btn {
  margin-right: 15px;
  padding: 10px 25px;
  background: #fff;
  border: 1px solid green;
  outline: none;
  cursor: pointer;
  color: #0f0f0f;
  font-size: 16px;
  font-weight: 400;
}

.cuaranteed_tours .reviews_icon {
  margin-left: 2px;
}

.cuaranteed_tours .buttons_category .btn.active {
  color: #fff;
  background: green;
  border: none;
}

.cuaranteed_tours .tour_recuest {
  width: 100%;
  min-height: 200px;
  padding: 25px;
  background: #fff;
  margin: 40px 0 0 0;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
}

.cuaranteed_tours .tour_recuest h1 {
  color: green;
  font-size: 33px;
  font-weight: 700;
}

.cuaranteed_tours .tour_recuest p {
  color: #000;
  font-size: 14px;
  font-weight: 300;
}

.cuaranteed_tours .tour_recuest h3 {
  margin-top: 25px;
  color: green;
  font-size: 16px;
  font-weight: 600;
}

.cuaranteed_tours .tour_recuest .data {
  margin-top: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 22px;
  row-gap: 15px;
}

.cuaranteed_tours .tour_recuest textarea,
.cuaranteed_tours .tour_recuest .data input {
  width: 100%;
  padding: 10px 14px;
  outline: none;
  border: 1px solid green;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
  color: #0f0f0f;
  font-size: 16px;
  font-weight: 400;
}

.cuaranteed_tours .tour_recuest textarea {
  height: 110px;
  margin-top: 10px;
}

.cuaranteed_tours .tour_recuest .send {
  margin-top: 15px;
  padding: 10px 70px;
  outline: none;
  border: none;
  cursor: pointer;
  color: #fcfcfc;
  font-size: 18px;
  font-weight: 600;
  background: green;
}

.cuaranteed_tours .input_box {
  position: relative;
}

.cuaranteed_tours .icon {
  position: absolute;
  top: 10px;
  right: 10px;
  color: green;
  cursor: pointer;
}

.cuaranteed_tours .went {
  color: #000;
  font-size: 22px !important;
  font-weight: 700 !important;
  line-height: 30px;
}

.cuaranteed_tours .tour_all {
  width: 100%;
  margin-top: 20px;
}

.cuaranteed_tours .tour_all .tour_box {
  cursor: pointer;
  width: 100%;
  height: 236px;
  margin-bottom: 20px;
  position: relative;
}

.cuaranteed_tours .tour_all .tour_image {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
}
em {
  font-size: 14px;
  color: #000;
  font-weight: 300;
  padding: 30px 0 0 0;
  margin: 30px 0 0;
}

.cuaranteed_tours .tour_all .tour_image img {
  width: 100%;
  height: 100%;
}

.cuaranteed_tours .tour_all .content {
  bottom: 0;
  width: 100%;
  position: absolute;
  padding: 13px;
  width: 100%;
  display: grid;
  grid-template-columns: 2fr 1fr;
  z-index: 10;
}

.cuaranteed_tours .tour_all .content_block {
  width: 100%;
  display: flex;
  background-color: rgba(0, 0, 0, 0.3);
  grid-template-columns: 2fr 1fr;
  align-items: center;
  position: absolute;
  bottom: 0;
  padding: 10px;
  z-index: 15;
}

.content_block .content_box {
  width: 100%;
}

.cuaranteed_tours .tour_all .content h1 {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.cuaranteed_tours .tour_all .content h2 b {
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
}

.cuaranteed_tours .tour_all .content h2 span {
  color: #fff;
  font-size: 24px !important;
  font-weight: 600 !important;
  margin-left: 15px !important;
}

.cuaranteed_tours .tour_all .content p {
  color: #fff;
  font-size: 13px;
  font-weight: 300;
}

.cuaranteed_tours .tour_all .content h2 {
  margin-top: 5px;
  color: #fff;
  font-size: 14px;
  font-weight: 300;
  display: flex;
  align-items: center;
}

.cuaranteed_tours .content .box_h1 {
  color: #fff;
  font-size: 24px !important;
  font-weight: 600 !important;
  margin-left: 15px !important;
}
@media (max-width: 900px) {
  .cuaranteed_tours .wrapper_two {
    display: flex;
    flex-direction: column;
  }

  .cuaranteed_tours .tours_all {
    margin-top: 30px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    gap: 50px;
  }
}

@media (max-width: 450px) {
  .cuaranteed_tours .buttons_category {
    width: 100%;
    margin: 30px 0 30px 0;
    overflow-x: auto;
    white-space: nowrap;
  }
}
