.gallery_detail .as_nav_for {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(310px, 1fr));
    grid-gap: 32px;
    grid-auto-flow: dense;
    margin: 60px 0 0 0;
}

.gallery_detail .as_nav_for img {
    width: 100%;
    height: 70%;
    cursor: pointer;
}

.gallery_detail .gallery_slieder {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 1);
    z-index: 10000;
    display: flex;
    align-items: center;
    justify-content: center;
}

.slider_gallery_detail .img_slider {
    width: auto;
    margin: 0 auto;
    height: 80%;
    z-index: 111;
}

.slider_gallery_detail .img_slider img {
    width: 600px;
    height: 400px;
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: center;
    object-position: center;
    cursor: zoom-in;
    margin: 0 auto;
}

.slider_gallery_detail .slider_img {
    width: 96%;
    height: 65px;
    margin: 10px auto;
    cursor: pointer;
}

.slider_gallery_detail .slider_img img {
    width: 95%;
    height: 100%;
    margin: 10px auto;
    cursor: pointer;
}

@media (max-width: 450px) {
    .slider_gallery_detail .img_slider img {
        height: 200px;
    }

    .slider_gallery_detail .slider_img img {
        height: 20px;
    }

}

