.modal_successss {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.modal_successss .modal {
  background: white;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  position: relative;
  width: 90%;
  max-width: 400px;
  display: flex;
  align-items: center;
  border: 1px solid #14ae5c;
  justify-content: center;
  flex-direction: column;

}

.modal_successss .sucess {
  width: 70%;
  margin-bottom: 10px;
}

.modal_successss .btn {
  display: flex;
  width: 100%;
  padding: 12px 18px;
  justify-content: center;
  color: #fff;
  align-items: center;
  gap: 10px;
  outline: none;
  border: none;
  border-radius: 20px;
  background: #14ae5c;
}

.modal_successss .svami {
  color: var(--Gray-2, #4f4f4f);
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  line-height: 140%;
  margin-top: 10px;
  letter-spacing: 0.32px;
  margin-bottom: 10px;
}

@media screen and (max-width: 768px) {
  .modal_successss .modal {
    width: 90%;
  }
}
