.footer {
  width: 100%;
  padding: 50px 0 0 0;
  background: #333333;
  margin-top: 60px;
  border-top: 1px solid #ddd;
}

.footer .footer_image {
  margin: 0 auto;
  padding-bottom: 50px;
  max-width: 1540px;
  width: 100%;
  position: relative;
}

.footer .wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: 3fr 3fr 3fr 3fr 3fr;
  gap: 60px;
  justify-content: center;
}

.footer .page_box {
  display: flex;
  flex-direction: column;
}

.footer .page {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
  margin-bottom: 15px;
  white-space: nowrap;
}

.footer .page:before {
  content: '';
  width: 10px;
  height: 1px;
  background: #fff;
}

.footer_copywrite {
  padding: 10px 0;
  background: #111111;
}

.footer_copywrite .container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.footer_copywrite small {
  display: block;
  color: #ffffff;
  font-size: 12px;
  font-weight: normal;
}

.payment-icons {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.payment-icon {
  display: block;
  width: 50px;
  height: 30px;
  background: #fff;
}

.payment-icon.visa {
  background: url('../../img/visa-svgrepo-com.svg') no-repeat center;
  background-size: 100%;
}

.payment-icon.master-card {
  background: url('../../img/mastercard-svgrepo-com.svg') no-repeat center;
  background-size: 100%;
}

.footer .page_box p {
  color: #0f0f0f;
  font-size: 14px;
  font-weight: 300;
  line-height: normal;
  margin-bottom: 17px;
}

.footer .icons_box {
  width: 120%;
  margin: 0 0 0 -35px;
}

.footer .left_bold_img {
  left: 0;
  top: 0;
  position: absolute;
  opacity: 0.2;
}

.footer .right_bold_img {
  right: 0;
  top: 0;
  position: absolute;
  color: #eaf5ef;
  opacity: 0.2;
}

.footer .flex_life ul li {
  display: flex;
  align-items: center;
  list-style: none;
  position: relative;
  color: #14ae5c;
  font-size: 14px;
  font-weight: 400;
  padding: 10px;
  line-height: 1;
}

.footer .flex_life ul li .linee {
  border-right: 1px solid #ddd;
}

.footer .flex_life ul li a {
  color: #ffffff !important;
  font-size: 13px;
  font-weight: 300;
}

.footer .flex_life ul li a:hover {
  text-decoration: underline;
}
.footer .fill {
  color: #04c700;
}
.footer .fills {
  color: #04c700;
}
.footer .bat {
  font-size: 18px;
  color: #04c700;
  font-weight: 400;
  margin-bottom: 10px;
}
.footer .wrap_img {
  width: 200px;
}
@media (max-width: 660px) {
  .footer .wrapper {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    justify-content: center;
  }

  .footer .blocks_all {
    margin-top: 20px;
  }

  .footer .icons_box {
    width: 120%;
    margin: 0 0 0 4px;
  }
  /* .footer .wrap_img{
        width: 100%;
    } */
}

@media (max-width: 860px) {
  .footer .wrapper {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    justify-content: center;
  }

  .footer .blocks_all {
    margin-top: 20px;
  }

  .footer .icons_box {
    width: 120%;
    margin: 0 0 0 4px;
  }
}

@media (max-width: 460px) {
  .footer .wrapper {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
    justify-content: center;
  }

  .footer .blocks_all {
    margin-top: 20px;
  }

  .footer .icons_box {
    width: 120%;
    margin: 0 0 0 4px;
  }
  .footer .fills {
    margin: 0 8px 0 0;
    font-size: 16px !important;
  }
}
