.as_nav_for .as_nav img {
    display: block;
    height: 400px;
    width: 98.5% !important;
    margin: 0 auto;
}

.as_nav_for .as_for img {
    width: 95% !important;
    height: 100px;
    cursor: pointer;
    margin: 10px auto;
}
.as_nav_for .alls img {
    position: relative;
    cursor: pointer;
    height: 100px;
    width: 95%;
    margin: 10px 0 0 0;
}
.as_for img:empty::before {
    width: 0;
    height: 0;
    content: '';
}

.as_nav_for .alls {
    position: relative;
    cursor: pointer;
}

.as_nav_for .alls:before {
    position: absolute;
    background: rgba(0, 0, 0, 0.1);
    content: "";
    width: 95%;
    height: 100px;
    left: 0;
}

.as_nav_for .video {
    position: absolute;
    color: #FFFFFF;
    font-size: 50px;
    top: 34px;
    left: 0;
    bottom: 0;
    right: 0;
    margin: 0 auto;
}


.as_nav_for .iframe {
    display: block;
    height: 400px;
    width: 98.5% !important;
    margin: 0 auto;
}

.as_nav_for .iframes {
    width: 95% !important;
    height: 100px;
    cursor: pointer;
    margin: 10px auto;
}
.as_nav_for .as_for .slick-slide {
    width: auto !important;
}

@media (max-width: 500px) {
    .as_nav_for .as_nav img {
        height: 200px;
    }
    .as_nav_for .iframe {
        height: 200px;
    }
    .as_nav_for .as_for img {
        height: 70px;
    }
    .as_nav .slick-list {
        height: 200px!important;
    }
    .as_nav_for .video {
        position: absolute;
        color: #FFFFFF;
        font-size: 40px;
        top: 17px;
        left: 0;
        bottom: 0;
        right: 0;
        margin: 0 auto;
    }
    .as_nav_for .alls:before {
        position: absolute;
        background: rgba(0, 0, 0, 0.1);
        content: "";
        width: 95%;
        height: 71px;
        left: 0;
    }
}
