

#category_tour .category h2{
    color: #333;
    background-color: #f5f5f5;
    border-color: #ddd;
    font-size: 18px;
    padding: 10px 15px;
}
#category_tour .category .category_one{
    border: 1px solid #ddd;
    padding: 10px 15px;
}
#category_tour  .a_teg{
    color: #0091cc;
    font-size: 16px;
    font-weight: 400;
}
#category_tour .a_teg:hover{
    text-decoration: underline;
}



#category_tour  .article_grit {
    display: grid;
    grid-template-columns: 2.5fr 1fr;
    gap: 30px;
}


#category_tour {
    margin-bottom: 20px;
    border: 1px solid #ddd;
    color: red;
}

#category_tour  img {
    width: 300px;
}

#category_tour .tour_box .content_box h1 {
    font-weight: 500;
    color: #000;
    font-size: 18px;
    line-height: 18px;
    margin: 20px 20px 0 20px;
    height: 34px;
    overflow: hidden;
}

#category_tour .tour_box .content_box h2 {
    font-weight: 300;
    color: #000;
    font-size: 14px;
    line-height: 16px;
    padding: 15px 20px 0 20px;
    height: 96px;
    overflow: hidden;
}

#category_tour .tour_box .content_box p {
    overflow: hidden;
    height: 100px;
    margin-bottom: 10px;
    margin-left: 10px;
    color: #4f5d6e;
    font-size: 16px;
}

#category_tour .tour_box .content_box .btn {
    overflow: hidden;
    padding: 12px 25px;
    margin: 20px;
    font-size: 14px;
    background: #14AE5C;
    color: #fff;
    border: none;
    cursor: pointer;
}

#category_tour .category h2 {
    color: #333;
    background-color: #f5f5f5;
    border-color: #ddd;
    font-size: 16px;
    font-weight: 500;
    border-bottom: 1px solid #ddd;
}

#category_tour .category .category_one {
    border: 1px solid #ddd;
    padding: 10px 15px;
}

#category_tour .a_teg {
    color: #0091cc;
    font-size: 16px;
    font-weight: 400;
    cursor: pointer;
}

#category_tour .a_teg:hover {
    text-decoration: underline;
}

