.modal {
  width: 100%;
  position: absolute;
  left: 0;
  top: 72px;
  background: #eaf5ef;
  -webkit-backdrop-filter: blur(25px);
  backdrop-filter: blur(25px);
  padding: 20px 20px 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  overflow: hidden;
  z-index: 999;
  margin-top: -10px;
  display: none;
}

.modal-item {
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  display: block;
  padding: 19px 0;
  border-bottom: 1px solid #ffffff;
  margin-left: -1px;
}

.kyrgyzstan {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #000;
  cursor: pointer;
  padding: 19px 0;
  border-bottom: 1px solid #ffffff;
  text-transform: uppercase;
  font-weight: 400;
}

.ul_li_a a {
  color: #000 !important;
  text-decoration: none;
  text-transform: uppercase;
  display: block;
  padding: 19px 0;
  border-bottom: 1px solid #ffffff;
  margin: 0 0 10px 20px;
}

@media (max-width: 800px) {
  .modal {
    display: block;
  }
}
