.tour_home {
  margin-top: 50px;
  width: 100%;
}

.text_title {
  margin-bottom: 20px;
}

.text_lorem {
  font-size: 18px;
  margin-bottom: 20px;
}

.tour_home .tour_box {
  width: 100%;
  min-height: 220px;
  display: flex;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr 2.5fr;
  gap: 30px;
  padding-right: 30px;
  margin-bottom: 30px;
  background: #fff;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.15);
}

.tour_home .tour_box img {
  width: 100%;
  height: 284px;
  object-fit: cover;
  object-position: center;
}

.tour_home .content_container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.tour_home .August {
  display: flex;
  align-items: center;
  font-size: 13px;
  font-weight: 400;
  gap: 10px;
  margin-bottom: 15px !important;
}

.tour_home .content_box h2 {
  color: #1a1a1a;
  font-size: 22px;
  font-weight: 700;
}

.tour_home .colored-star {
  color: yellow;
}

.tour_home .content_box p {
  color: #4f4f4f;
  font-size: 14px;
  font-weight: 300;
  line-height: 16px;
  height: 96px;
  overflow: hidden;
}

.tour_home .content_box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 70%;
  gap: 15px;
}

.tour_home .content_box_two {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.tour_home .content_box_two p {
  width: 100%;
  display: block;
  color: #0f0f0f;
  text-align: right;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  margin: 18px 0 0 0;
}

.tour_home .content_box_two .price_div {
  margin-left: auto;
  width: 100px;
  padding: 10px 15px;
  background: var(--green);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 17px;
  font-weight: 500;
}

.tour_home .paginatio {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.tour_home .paginatio button {
  margin: 0 0;
  padding: 5px 5px;
  cursor: pointer;
}

.tour_home .paginatio button {
  border: none;
  background: transparent;
}

.tour_home .paginatio .arrow_left {
  font-size: 25px;
  color: #14ae5c;
}

.tour_home .paginatio .arrow_right {
  font-size: 25px;
  color: #14ae5c;
}

.tour_home .pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.tour_home .pagination button {
  margin: 0 0;
  padding: 5px 5px;
  cursor: pointer;
}

.tour_home .pagination h5 {
  color: #121;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.tour_home .pagination button {
  border: none;
  background: transparent;
}

.tour_home .pagination .arrow_left {
  font-size: 25px;
}
.tour_home .pagination .arrow_left:disabled {
  color: #ccc;
  cursor: not-allowed;
}
.tour_home .pagination .arrow_right:disabled {
  color: #ccc;
  cursor: not-allowed;
}

.tour_home .pagination .arrow_right {
  font-size: 25px;
  margin: 3px 0 0 0;
}

.main-categories-title {
  margin-bottom: 20px;
  text-align: center;
}

.article-list {
  margin-bottom: 20px;
}

@media (max-width: 950px) {
  .tour_home .tour_box img {
    width: 100%;
  }

  .tour_home .tour_box {
    padding-right: 0;
  }

  .tour_home .tour_box {
    display: flex;
    flex-direction: column;

    align-items: center;
    gap: 30px;
  }

  .tour_home .content_container {
    margin-top: 0;
  }

  .tour_home .content_container {
    display: flex;

    align-items: center;
    gap: 10px;
    margin-left: 20px;
  }

  .tour_box p {
    margin: 0;
  }

  .tour_home .content_box_two .price_div {
    margin-left: 0;
    margin-bottom: 20px;
  }
}

@media (max-width: 850px) {
  .tour_home .tour_box {
    display: inline-flex;
    flex-direction: column;
    column-gap: normal;
    align-items: center;
  }

  .tour_home .content_box_two p {
    text-align: start;
    flex-direction: column;
  }

  .tour_home .content_container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    margin-left: 20px;
  }

  .tour_box p {
    margin: 0;
  }
}

@media (max-width: 450px) {
  .tour_home .tour_box {
    display: flex;
    align-items: flex-start;
  }

  .tour_home .content_box {
    width: 100%;
  }

  .tour_home .content_container {
    padding: 0 20px 0 0;
  }

  .tour_home .content_box h2 {
    line-height: 1.1;
  }

  .tour_home .content_box p {
    width: 100%;
  }

  .tour_home .content_box_two p {
    text-align: start;
    margin-top: 10px;
  }

  .tour_home .content_box .btn {
    margin-top: 10px;
  }

  .tour_home .text_title {
    font-size: 22px;
  }

  .tour_home .text_lorem {
    font-size: 14px;
  }
}

.tour_home .article_grit {
  display: grid;
  grid-template-columns: 2.5fr 1fr;
  gap: 30px;
}

.tour_home .tour_box {
  margin-bottom: 20px;
  border: 1px solid #ddd;
}

.tour_home .tour_box img {
  width: 300px;
}

.tour_home .tour_box .content_box h3 {
  font-weight: 500;
  color: #000;
  font-size: 18px;
  line-height: 18px;
  overflow: hidden;
  margin: 10px 0 20px 0;
}

.tour_home .tour_box .content_box h2 {
  font-weight: 300;
  color: #000;
  font-size: 14px;
  line-height: 16px;
  padding: 15px 20px 0 20px;
  height: 96px;
  overflow: hidden;
}

.tour_home .tour_box .content_box p {
  overflow: hidden;
  height: 100px;
  color: #4f5d6e;
  font-size: 13px;
  width: 90%;
}

.tour_home .content_box .btn {
  outline: none;
  border: none;
  margin-bottom: 37px;
  cursor: pointer;
  padding: 10px 20px;
  color: #fcfcfc;
  font-size: 16px;
  font-weight: 500;
  background: #14ae5c;
}

.tour_home .category p {
  color: #333;
  background-color: #f5f5f5;
  border-color: #ddd;
  font-size: 16px;
  font-weight: 500;
  padding: 0 15px;
  border-bottom: 1px solid #ddd;
  margin: 0;
  line-height: 40px;
  cursor: pointer;
  transition: all 0.5s ease;
}

.tour_home .category p:hover {
  background-color: #d6d4d4;
}

.tour_home .category .category_one {
  border: 1px solid #ddd;
  padding: 10px 15px;
}

.grid_flex {
  display: grid;
  grid-template-columns: 2.5fr 1fr;
  gap: 30px;
}

.tour_home .a_teg {
  color: #0091cc;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
}

.tour_home .a_teg:hover {
  text-decoration: underline;
}

.current-page {
  font-size: 18px;
}

@media (max-width: 835px) {
  .grid_flex {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .tour_home .tour_box img {
    width: 100% !important;
  }

  .tour_home .tour_box .content_box h2 {
    margin: 20px 0 0 0;
  }

  .tour_home .tour_box .content_box p {
    margin-left: 0;
    width: 100%;
  }
}

@media (max-width: 820px) {
  .grid_flex {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .tour_home .tour_box img {
    width: 100% !important;
  }

  .tour_home .tour_box .content_box h2 {
    margin: 20px 0 0 0;
  }

  .tour_home .tour_box .content_box p {
    margin-left: 0;
    width: 100%;
  }
}

@media (max-width: 650px) {
  .grid_flex {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .tour_home .tour_box img {
    width: 100% !important;
  }

  .tour_home .tour_box .content_box h1 {
    margin: 20px 0 0 0;
  }

  .tour_home .tour_box .content_box p {
    width: 100%;
  }
}
