#rewievs {
    width: 100%;
    margin: 60px 0 0 0;
}


#rewievs .margin{
    padding: 0 8px;
    margin: 0;
    transform: scale(1);
    transition: margin 0.3s ease, transform 0.3s ease;
}
#rewievs .margin:hover{
    margin: -10px 0 0 0;
    transform: scale(1.02);
    cursor: pointer;
}

#rewievs .block {
    padding: 30px;
    flex: 0 0 33.333%;
    background: rgba(20, 174, 92, 0.07);
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.05);
    margin: 20px 0 0 0;
}

#rewievs .block .all {
    display: flex;
    flex-direction: column;
}


#rewievs .block .all img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 0 0 10px 0;
}
#rewievs .block .all .flex_one{
    display: flex;
    align-items: center;
}

#rewievs .block .all h4 {
    font-weight: 500;
    font-size: 15px;
    overflow: hidden;
    padding-right: 5px;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #000;
    margin-bottom: 2px;
}

.star.active {
    color: #ffce00;
    cursor: pointer;
}

#rewievs .block .all p {
    margin: 10px 0 0 0;
    color: #4F5D6E;
    font-size: 14px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
}

#rewievs .slick-prev {
    line-height: 0;
    width: 40px;
    height: 40px;
    padding: 0;
    cursor: pointer;
    position: relative;
}

#rewievs .fistart{
    color: #ffce00;
}
#rewievs .slick-prev::before {
    position: absolute;
    border-radius: 50%;
    border: 2px solid white;
    background: #14AE5C;
    width: 25px;
    height: 25px;
    margin: 200px 0 0 -29px;
    content: url("../../img/right-arrow 5.svg");
    color: white;
    transform: rotate(180deg);
    display: flex;
    z-index: 9999;
    align-items: center;
    justify-content: center;
}

#rewievs .slick-next {
    line-height: 0;
    width: 40px;
    height: 40px;
    padding: 0;
    cursor: pointer;
    position: relative;
}

#rewievs .slick-next::before {
    position: absolute;
    border-radius: 50%;
    border: 2px solid white;
    background: #14AE5C;
    width: 25px;
    height: 25px;
    margin: -157px 0 0 1140px;
    content: url("../../img/right-arrow 5.svg");
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

#rewievs .star {
    font-size: 17px;
}

@media (max-width: 980px) {
    #rewievs .wrapper {
        margin-left: 81px;
    }
}
@media (max-width: 428px) {
    #rewievs .slick-prev::before {
        display: none;
    }
    #rewievs .slick-next::before {
        display: none;
    }
    #rewievs .wrapper {
        margin-left: 0;
    }
}

@media (max-width: 414px) {
    #rewievs .wrapper {
        margin-left: 0;
    }

    #rewievs .block {
        margin-left: 0;
    }

    #rewievs .slick-prev {
        display: none;
    }

    #rewievs .slick-prev::before {
        display: none;
    }

    #rewievs .slick-next {
        display: none;
    }

    #rewievs .slick-next::before {
        display: none;
    }
}