#header {
  position: sticky;
  top: 0;
  z-index: 100000;
  border-bottom: 1px solid #ddd;
  background-color: #fff;
}

.header-inner {
  padding: 15px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation {
  display: flex;
  align-items: center;
}

.nav-item {
  display: flex;
  align-items: center;
  color: #14ae5c;
  font-size: 12px;
  padding: 10px;
  border-right: 1px solid #ddd;
}

.nav-item a {
  color: #000;
}

.nav-item a:hover {
  color: #0085ba;
}

.lang-select {
  display: none;
}

.lang {
  display: flex;
  align-items: center;
  gap: 5px;
}

@media (max-width: 800px) {
  .navigation {
    display: none;
  }
}

@media (max-width: 570px) {
  .languages {
    position: relative;
  }

  .lang-select {
    display: block;
    display: flex;
    justify-content: space-between;
    width: 200px;
    color: #fff;
    background-color: #14ae5c;
    padding: 8px 5px;
  }

  .lang-select div {
    display: flex;
    flex-direction: column;
  }

  .lang-list {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: #fff;
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  .lang {
    display: none;
  }
}
