.contact_details {
  width: 100%;
}
.contact_details .create_center {
  margin-top: 70px;
  width: 100%;
  min-height: 500px;
  padding: 25px;
  gap: 25px;
  background: #fff;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
}
.form-title {
  color: #14ae5c;
  font-weight: 500;
  line-height: 39px;
}
.contact_details .create_center p {
  color: #000;
  font-size: 14px;
  font-weight: 300;
  line-height: 16px;
  margin-top: 7px;
  margin-bottom: 10px;
}
.contact_details .create_center h3 {
  color: #14ae5c;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  margin: 20px 0 8px;
}
.contact_details .ctegory_block {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}
.contact_details .category_box {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 14px;
  gap: 8px;
  border: 1px solid #14ae5c;
  background: #fff;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
  color: #0f0f0f;
  cursor: pointer;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  outline: none;
}
.contact_details .select_boxx {
  margin-top: -7px;
}
.contact_details .textarea__top ::placeholder {
  color: #e00909;
}
.contact_details .textarea__top {
  width: 100%;
  min-height: 100px;
  padding: 10px 14px;
  border: 1px solid #14ae5c;
  font-size: 14px;
  font-weight: 300;
  outline: none;
  background: #fff;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
.contact_details .textarea::placeholder {
  color: #0f0f0f;
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  margin-top: -20px;
}
.contact_details .date_block {
  margin-top: 10px;
  display: flex;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.date-send-btn {
  margin: 0 auto;
  width: 50%;
  padding: 10px;
  background: #14ae5c;
  border: none;
  outline: none;
  color: #fcfcfc;
  font-size: 18px;
  font-weight: 600;
  border-radius: 10px;
}
.contact_details .category_box::placeholder {
  color: #000;
}
.contact_details .input_box {
  position: relative;
}
.contact_details .icon {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
.contact_details .select_input {
  top: 0;
  position: absolute;
  background: #00000000;
  height: 42px;
  cursor: pointer;
}
.contact_details .input_cech {
  height: 16px;
}
.contact_details .tours_one {
  display: grid;
  font-size: 20px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 20px;
  margin-top: 15px;
}
.contact_details .tours_one p {
  font-size: 15px;
  margin-left: 8px;
}
@media (max-width: 900px) {
}
@media (max-width: 650px) {
  .contact_details .date_block {
    display: grid;
    grid-template-columns: 1fr;
  }

  .date-send-btn {
    width: 100%;
  }
}
@media (max-width: 400px) {
  .contact_details .ctegory_block {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
  .contact_details .create_center h1 {
    font-size: 20px;
  }
}
