.rent_car .car_title {
  color: #000;
  font-size: 30px;
  font-weight: 500;
  line-height: normal;
  margin-top: 50px;
}

.rent_car .car_text {
  color: #000;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  width: 80%;
}

.rent_car .car_option_btn {
  width: 100%;
  display: flex;
  align-items: center;
}

.rent_car .car_option_btn .button {
  margin-right: 10px;
  margin-top: 30px;
  padding: 7px 25px;
  border: 1px solid #14ae5c;
  background: none;
  color: #0f0f0f;
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
  cursor: pointer;
}

.rent_car .car_option_btn .cart_button {
  background: #14ae5c;
  color: white;
}

.rent_car .tour_box {
  margin: 30px 0 0 0;
  cursor: pointer;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
  grid-gap: 32px;
  grid-auto-flow: dense;
}

.rent_car .content {
  position: relative;
}
.rent_car .content img {
  width: 100%;
  height: 220px;
  object-fit: cover;
  object-position: center;
  position: relative;
}
.rent_car .content_box {
  content: '';
  bottom: 4px;
  left: 0;
  height: 65px;
  width: 100%;
  position: absolute;
  padding: 13px;
  background: rgba(0, 0, 0, 0.41) 57.3%;
  box-shadow: 0 2px 25px 0 rgba(0, 0, 0, 0.2);
  display: grid;
  grid-template-columns: 2fr 1fr;
}

.rent_car .content_box h2 {
  color: #fff;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
}

.rent_car .content_box .all {
  color: #fff;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
}

.rent_car .content_box .all h4 {
  color: #fff;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}

@media (max-width: 700px) {
  .rent_car .car_option_btn {
    display: flex;
    overflow-y: hidden;
  }
}

@media (max-width: 600px) {
  .rent_car .ctegory_block {
    width: 100%;
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
  }

  .rent_car .car_title {
    font-size: 20px;
    margin-bottom: 10px;
  }
}
