.slider {
  width: 100%;
  position: relative;
  margin-top: 0;
}

.slider img {
  width: 100%;
  height: 420px;
  position: relative;
  object-fit: cover;
  object-position: center;
}

.slider_box {
  width: 390px;
  min-height: 212px;
  position: absolute;
  top: 188px;
  left: 20px;
}

.slider_box p {
  color: #fff;
  font-size: 22px;
  font-weight: 500;
  max-width: 300px;
}

.slider_box p {
  color: rgba(255, 255, 255, 1);
  font-size: 14px;
  font-weight: 300;
  line-height: 1.1;
  margin: 10px 0;
  max-width: 400px;
}

.slider_box button {
  margin-top: 0;
  padding: 8px;
  background: #14ae5c;
  color: #fcfcfc;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  border: none;
  outline: none;
  cursor: pointer;
}

.slider-block {
  position: relative;
}

.slider-block:after {
  content: '';
  width: 15%;
  height: 120px;
  background: url('../../../img/oop.png') center center no-repeat;
  background-size: 100%;
  position: absolute;
  top: 5%;
  right: 13%;
  z-index: 111;
}

@media (max-width: 570px) {
  .my-container {
    width: 100% !important;
  }

  .slider img {
    height: 270px;
  }

  .slider_box {
    top: 125px;
  }

  .slider-block:after {
    width: 40%;
    right: -2%;
  }
}
