#categories {
  margin: 30px auto;
  position: relative;
}

#categories::before {
  content: '';
  background-size: 1000px;
  background-position: right bottom;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;

  position: absolute;
  left: 0;
  bottom: -230px;
  z-index: -1;
}

#categories .img_home {
  position: absolute;
  min-height: 200px;
  max-width: 1540px;
  margin: -130px auto 0 103%;
  z-index: 1;
}

#categories .tour_all {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(310px, 1fr));
  grid-gap: 32px;
  grid-auto-flow: dense;
  margin-top: 30px;
}

#categories .tour_all .tour_box {
  cursor: pointer;
  width: 100%;
  height: 240px;
  position: relative;
  overflow: hidden;
}

#categories .tour_all .tour_image img {
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  object-position: center;
}

#categories .tour_all .tour_image {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
}

#categories .tour_all .content {
  bottom: 0;
  display: grid;
  grid-template-columns: 2fr 1fr;
  padding: 24px;
  position: absolute;
  width: 100%;
  z-index: 10;
}

#categories .tour_all .content_block {
  align-items: center;
  background-color: rgba(0, 0, 0, 0.3);
  bottom: 0;
  display: flex;
  grid-template-columns: 2fr 1fr;
  padding: 10px;
  position: absolute;
  width: 100%;
  z-index: 15;
}

.content_block .content_box {
  width: 100%;
}

#categories .tour_all .content h3.content_box-item {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

#categories .tour_all .content h3.content_box-item p {
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
}
#categories .tour_all .content h3.content_box-item p:last-child {
  color: #fff;
  font-size: 21px !important;
  font-weight: 600 !important;
  margin-left: 15px !important;
}

#categories .tour_all .content p {
  color: #fff;
  font-size: 13px;
  font-weight: 300;
}

#categories .tour_all .content h2 {
  color: #fff;
  font-size: 14px;
  font-weight: 300;
  display: flex;
  align-items: center;
}

#categories .content .box_h1 {
  color: #fff;
  font-size: 24px !important;
  font-weight: 600 !important;
  margin-left: 15px !important;
}

#categories button {
  padding: 10px 30px;
  color: #ffffff;
  font-size: 16px;
  font-weight: 400;
  border: none;
  background-color: #14ae5c;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

#categories .home_none {
  display: none;
}

@media (max-width: 550px) {
  #categories .home_none {
    display: block;
  }
  #categories .div {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }
}

@media (max-width: 428px) {
  #categories .tour_all .tour_box {
    width: 100%;
    height: 140px;
    margin: 0 auto;
  }

  #categories button {
    margin: 20px 0 0 0;
  }
}
#categories .tour_box img {
  width: 300px;
}

#categories .tour_box .content_box h3.content_box-item {
  font-weight: 500;
  color: #000;
  font-size: 18px;
}

#categories .tour_box .content_box p {
  font-weight: 300;
  color: #fff;
  font-size: 14px;
  overflow: hidden;
}

#categories .tour_box .content_box .btn {
  overflow: hidden;
  padding: 12px 25px;
  margin: 20px;
  font-size: 14px;
  background: #14ae5c;
  color: #fff;
  border: none;
  cursor: pointer;
}

.title-articles {
  text-align: center;
  margin-bottom: 20px;
}

.grid_flex .categorys {
  color: #333;
  background-color: #f5f5f5;
  border-color: #ddd;
  font-size: 16px;
  font-weight: 500;
  border-bottom: 1px solid #ddd;
  margin: 0 0 0 0;
}

.category .category_one {
  border: 1px solid #ddd;
  padding: 10px 15px;
}

.category_list .a_teg {
  color: #0091cc;
  font-size: 16px;
  font-weight: 400;
  cursor: pointer;
  border-bottom: 1px solid #ddd;
  padding: 10px 15px;
}

.category_list .a_teg:hover {
  text-decoration: underline;
}
.category_all h3 {
  color: #333;
  background-color: #f5f5f5;
  border-color: #ddd;
  font-size: 18px;
  font-weight: 500;
  padding: 5px 15px;
  border-bottom: 1px solid #ddd;
  margin: 0 0 0 0;
}
.category_all_one p {
  width: 100%;
  color: #333;
  background-color: #f5f5f5;
  border-color: #ddd;
  font-size: 18px;
  font-weight: 500;
  padding: 5px 15px;
  border-bottom: 1px solid #ddd;
  margin: 0 0 0 0;
  cursor: pointer;
  transition: all 0.5s ease;
}

.category_all_one p:hover {
  background-color: #d6d4d4;
}

.tours-title {
  text-align: center;
  margin-bottom: 15px;
}

.tours-description {
  margin-bottom: 20px;
  font-size: 18px;
}

@media (max-width: 550px) {
  #categories .tour_box img {
    width: 100%;
  }

  #categories .tour_box .content_box h3.content_box-item {
    margin: 0 0 0 0;
  }

  #categories .tour_box .content_box p {
    margin-bottom: -5px;
    margin-left: 0;
  }

  .tours-description {
    font-size: 16px;
  }
}
