.contacts {
  width: 100%;
  min-height: 1000px;
}
.contacts .connected {
  margin-top: 40px;
  color: #111;
  text-align: center;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  text-transform: uppercase;
}
.contacts .contacts .lets_talk {
  color: #030922;
  font-size: 53px;
  font-weight: 700;
  line-height: 70px;
}
.contacts .lin {
  width: 300px;
  height: 1px;
  flex-shrink: 0;
  background: var(--unnamed, #14ae5c);
}
.contacts .message {
  margin-top: 20px;
}
.contacts .lina {
  width: 300px;
  height: 1px;
  flex-shrink: 0;
  background: var(--unnamed, #14ae5c);
}
.contacts .talk {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
  line-height: 1;
}
.contacts .way {
  text-align: center;
  margin-top: 5px;
  color: #111111;
  font-size: 15px;
  font-weight: 300;
}
.contacts .request_blocks {
  width: 100%;
  min-height: 780px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 50px;
  margin-top: 30px;
}
.contacts .call_us_box {
  width: 100%;
}
.contacts .request_box {
  width: 100%;
  min-height: 750px;
  padding: 30px;
  border: 1px solid #14ae5c;
  background: #fff;
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.1);
}
.contacts .request_box h2 {
  color: #000;
  font-size: 20px;
  font-weight: 600;
  margin: 0 auto 15px auto;
}
.contacts .address {
  width: 100%;
  margin-bottom: 40px;
}
.contacts .address h2 {
  display: block;
  margin-bottom: 5px;
  color: #111;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  margin-left: 6px;
}
.contacts .address p {
  color: #000;
  font-size: 14px;
  font-weight: 300;
  line-height: normal;
}
.contacts .name_block_all {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
.contacts .name_block_all label {
  display: block;
  color: #111;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  line-height: 1;
  text-transform: uppercase;
  margin: 0 0 5px 0;
}
.contacts .name_block_all input {
  width: 100%;
  border: 1px solid #14ae5c;
  background: #fbfbfb;
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.02);
  outline: none;
  padding: 15px;
}
.contacts .name_block_all p {
  margin-top: 60px;
  color: var(--unnamed, #0f0f0f);
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
}
.contacts .name_block,
.contacts .first_name_block {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.contacts .textarea {
  width: 100% !important;
  margin-top: 10px;
  min-height: 100px;
  padding: 10px 14px;
  border: 1px solid #14ae5c;
  font-size: 16px;
  outline: none;
  background: var(--white, #fff);
  box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);
  transition: all 0.3s linear;
}

.contacts .checkbox_text {
  display: flex;
  margin-top: 10px;
  color: #000;
  font-size: 14px;
  font-weight: 300;
  line-height: 1;
  cursor: pointer;
}
.contacts .text1 {
  margin-top: 10px;
  color: #000;
  font-size: 14px;
  font-weight: 300;
}
.contacts .checkbox_text input {
  margin-right: 8px;
}
.contacts .request_btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  margin-top: 30px;
  width: 304px;
  padding: 10px;
  background: #14ae5c;
  border: none;
  outline: none;
  color: var(--unnamed, #fcfcfc);
  font-size: 18px;
  font-weight: 600;
  line-height: normal;
  cursor: pointer;
}
.contacts .kart_block {
  width: 100%;
  margin-top: 80px;
}
.contacts .kart_text {
  display: flex;
  gap: 8px;
  align-items: center;
  color: #111;
  font-size: 18px;
  font-weight: 500;
  line-height: 1;
}
.contacts .kart {
  margin-top: 30px;
  width: 220px;
  height: 220px;
  border: none;
  border-radius: 150px;
}
.contacts .renge {
  width: 100%;
  border-radius: 10px;
}
.contacts .value {
  margin-top: 10px;
  font-weight: 500;
  color: #275efe;
  display: flex;
  justify-content: space-between;
}
.contacts .values {
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #14ae5c;
}
.contacts .slider {
  width: 100%;
  height: 4px;
  background: #d9d9d9;
  border-radius: 1px;
}
.contacts .slider .thumb {
  margin-top: -5px;
  width: 15px;
  height: 15px;
  cursor: grab;
  background: var(--green);
  border-radius: 50%;
  border: 2px solid white;
  outline: none;
}
.contacts .slider .thumb::after {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid var(--green);
  background: white;
}
.contacts .track-1 {
  height: 4px;
  background: var(--green);
  border-radius: 100px;
}
.contacts .budget_text {
  color: var(--unnamed, #0f0f0f);
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 20.8px;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  margin-bottom: 5px;
}
.contacts .flex_life a {
  border-left: 1px solid #ffffff;
  color: #000;
  font-size: 14px;
  font-weight: 300;
}
.contacts .flex_life {
  display: flex;
  align-items: center;
  list-style: none;
  position: relative;
  color: #14ae5c;
  font-size: 12px;
  font-weight: 400;
  padding: 10px;
  line-height: 1;
}
@media (max-width: 1000px) {
  .contacts .lin,
  .contacts .lina {
    display: none;
  }
  .contacts .lets_talk {
    text-align: center;
  }
  .contacts .talk {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
@media (max-width: 800px) {
  .contacts .request_blocks {
    display: grid;
    grid-template-columns: 1fr;
  }
  .contacts .request_box h2 {
    font-size: 43px;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 35px;
  }
  .contacts .kart_block {
    width: 100%;
    margin-top: 50px;
  }
}
@media (max-width: 650px) {
  .contacts .name_block_all {
    display: grid;
    grid-template-columns: 1fr;
  }
  .contacts .lets_talk {
    font-size: 20px;
  }
}
@media (max-width: 450px) {
  .contacts .request_btn {
    width: 100%;
    min-height: 60px;
  }
}
